import {  useEffect,useState } from 'react'
import { getMarks } from '../../actions/markAction'
import { useAlert } from 'react-alert';

function FiltreMark({type,mark,handleFilters}) {
    const [marks, setMarks] = useState(null)
    const [erreur, setErreur] = useState(null)   
    const [checked, setChecked] = useState(mark)
    const alert = useAlert();
    useEffect(() => {
        getMarks(setMarks,setErreur,type)
    }, [type])
    useEffect(() => {
        if (erreur) {
            alert.error(<div style={{fontSize:'10px'}}>{erreur.errMessage?erreur.errMessage:'Erreur!!'}</div>)
            setErreur(null)
        }
    }, [erreur])
const conserverFiltre = (value) => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];
    if (currentIndex === -1) {
        newChecked.push(value)
    } else {
        newChecked.splice(currentIndex, 1)
    }
    setChecked(newChecked)
    handleFilters(newChecked)
}
    return (
    <div className="mt-5">
        {marks&&(<>
        <h4 className="mb-3">Marques</h4>
        <ul className="pl-0">
            {marks.marks.map(mar => (
                <div className="form-check" key={mar._id}>
                    <label className="form-check-label" htmlFor={mar.name}>
                        <input type="checkbox" defaultChecked={mark.indexOf(mar._id)>-1} className="form-check-input" id={mar.name} onChange={() => conserverFiltre(mar._id)} />{mar.name} <span style={{color:'grey',fontSize:'small'}}>({mar.numOfArticle})</span>
                    </label>
                </div>
            ))}
        </ul>
        </>)}
    </div>
    )
}

export default FiltreMark
